.menu {
  font-size: 16px;
  position: relative;
  left: 0;
  box-sizing: border-box;
  max-width: 308px;
  width: 100%;
  padding: 28.7px 24px 32px; }
  .menu .menu__wrap {
    max-height: 700px;
    display: block;
    box-sizing: border-box; }

.navigation__body-menu {
  margin-bottom: 1.9em; }
  .navigation__body-menu .navigation__block-menu {
    max-width: 153.8px; }

.navigation__item:hover {
  border: 1px solid #ff3e79;
  background-color: white; }

.navigation__item-menu_active {
  background-image: url(./static/cross.svg); }
  .navigation__item-menu_active:hover {
    background-repeat: no-repeat;
    background-position: center center;
    background-image: url(./static/cross-hover.svg); }

.navigation__item-menu_search {
  background-image: url(./static/search-menu.svg); }
  .navigation__item-menu_search:hover {
    background-repeat: no-repeat;
    background-position: center center;
    background-image: url(./static/search-menu-hover.svg); }

.main-menu__body {
  margin-left: -1.35em;
  margin-bottom: 14.6em; }
  .main-menu__body .main-menu__list {
    font-family: 'TT Lakes Regular';
    line-height: 24px;
    letter-spacing: -0.2px;
    color: #1b1c21; }

.main-menu__item {
  margin-bottom: 1.1em;
  height: 2em;
  padding-left: 1.66em;
