.service__technic {
  position: relative;
  background: #ffffff;
  border: 1px solid #eaeaea;
  border-radius: 6px;
  width: 100%;
  height: 160px;
  margin-bottom: 1em;
  padding: 1em 1em;
  display: flex;
  align-items: end;
  justify-content: end;
  background-repeat: no-repeat;
  background-position: 1em center; }
  .service__technic p {
    position: absolute;
    left: 0.95em;
    top: 0.85em;
    font-family: 'TT Lakes Medium';
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    color: #1b1c21; }
  .service__technic button {
    width: 40px;
    height: 40px;
    border: none;
    border-radius: 100%;
    background-color: rgba(0, 0, 0, 0);
    background-position: center center;
    background-image: url(./static/Brand-Go.svg);
    transition: 0.4s;
    cursor: pointer; }
    .service__technic button:hover {
      background-color: #ff3e79;
      background-image: url(./static/Brand-Go-active.svg); }

