@font-face {
  font-family: 'TT Lakes Light';
  src: url(./fonts/TTLakes-Light.woff) format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'TT Lakes Regular';
  src: url(./fonts/TTLakes-Regular.woff) format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'TT Lakes Medium';
  src: url(./fonts/TTLakes-Medium.woff) format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'TT Lakes DemiBold';
  src: url(./fonts/TTLakes-DemiBold.woff) format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'TT Lakes Bold';
  src: url(./fonts/TTLakes-Bold.woff) format("woff");
  font-weight: normal;
  font-style: normal; }

html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  max-width: 1430px;
  width: 100%;
  margin: 0 auto;
  font-size: 16px;
  margin: 0 auto; }

.container-all {
  display: grid;
  grid-template-columns: 308px 1fr; }

.container {
  position: relative;
  background: #f8f8f8; }

.container_hidden {
  opacity: 0.3;
  overflow: visible; }

.none-scroll {
  overflow: -moz-scrollbars-none !important;
  -ms-overflow-style: none !important;
  scrollbar-width: none !important;
  overflow-y: scroll !important;
  scrollbar-width: none !important;
  -ms-overflow-style: none !important; }
  .none-scroll::-webkit-scrollbar {
    width: 0 !important;
    height: 0 !important;
    display: none !important; }

.low-opacity {
  opacity: 0.3; }

.add-hidden {
  overflow: hidden; }

@media screen and (max-width: 1024px) {
  .container-all {
    display: block; } }
