@media screen and (max-width: 767.98px) {
  .swiper {
    width: 100%;
    height: 100%; }
  .swiper__brands {
    width: 100%;
    height: 117px;
    background-color: #f8f8f8; }
  .price {
    overflow: hidden; }
  .swiper__price {
    overflow: visible; }
    .swiper__price .swiper-pagination {
      position: relative;
      top: -20px; }
  .mySwiper {
    width: 100%;
    height: auto;
    background-color: #f8f8f8; }
  .swiper__technic {
    width: 100%;
    height: auto;
    background-color: #f8f8f8; }
  .swiper-slide {
    text-align: center;
    font-size: 16px;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: flex-start; }
    .swiper-slide img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover; }
  .swiper-pagination-bullet {
    color: #dddddd !important;
    background-color: #dddddd !important; }
  .swiper-pagination-bullet,
  .swiper-pagination-bullet-active {
    width: 12px !important;
    height: 12px !important; }
  .swiper-pagination-bullet-active {
    color: #b5b6bc !important;
    background-color: #b5b6bc !important; }
  .swiper-wrapper {
    box-sizing: border-box;
    display: flex;
    padding-right: 1em; }
  .swiper-slide,
  .swiper-slide-active {
    background-color: #f8f8f8; } }
