.feedback {
  position: absolute;
  right: 0;
  top: 0;
  max-width: 440px;
  width: 100%;
  z-index: 1200;
  background: #ffffff;
  height: auto;
  display: none;
  transition: 0.5s ease-in-out;
  -ms-overflow-style: none;
  overflow: -moz-scrollbars-none; }
  .feedback::-webkit-scrollbar {
    width: 0; }

.feedback_open {
  display: block; }

.feedback-phone {
  height: 100vh; }

.feedback__wrap {
  box-sizing: border-box;
  padding: 40px 32px;
  position: relative; }

.feedback__title {
  position: relative;
  left: -32px; }

.feedback__close-btn {
  position: absolute;
  left: -64px;
  background-image: url(./static/cross.svg);
  width: 40px;
  height: 40px;
  background-color: #ff3e79;
  border-radius: 100%;
  background-position: center center;
  background-repeat: no-repeat;
  border: none;
  transition: 0.5s;
  cursor: pointer; }
  .feedback__close-btn:hover {
    border: 1px solid #ff3e79;
    background-color: white;
    background-repeat: no-repeat;
    background-position: center center;
    background-image: url(./static/cross-hover.svg); }

.feedback__input {
  width: 100%;
  height: 48px;
  margin-bottom: 1em;
  background: #ffffff;
  border: 1px solid #eaeaea;
  border-radius: 8px;
  font-family: 'TT Lakes Regular';
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  color: #b5b6bc;
  padding: 12px 16px; }
  .feedback__input:focus-visible {
    outline: none; }

.feedback__textarea {
  height: 118px;
  resize: none;
  color: black; }

.feedback__text {
  font-family: 'TT Lakes Medium';
  font-size: 14px;
  line-height: 16px;
  color: #7e7e82;
  margin-bottom: 23px; }
  .feedback__text span {
    color: #ff3e79; }

.feedback__submit {
  float: right;
  padding: 0 1em;
  width: 136px;
  height: 36px;
  background: #ff3e79;
  border-radius: 16px;
  border: none;
  font-family: 'TT Lakes DemiBold';
  font-size: 12px;
  line-height: 24px;
  text-align: left;
  letter-spacing: -0.15px;
  text-transform: uppercase;
  color: #ffffff;
  background-image: url(./static/right-arrow.svg);
  background-repeat: no-repeat;
  background-position: 115px center;
  cursor: pointer;
  margin-bottom: 24px; }
