@media screen and (max-width: 768px) {
  .footer__right {
    max-width: 213px; } }

@media screen and (max-width: 767.97px) {
  .service__brands {
    -moz-column-gap: 0;
         column-gap: 0;
    overflow: visible; }
  .service__brands-more {
    display: none; }
  .price {
    padding: 0 1em; }
  .price__list {
    background: #f8f8f8; }
    .price__list .price__card {
      background: #ffffff;
      margin-bottom: 0.6em; }
  .price__caption {
    display: none; }
  .price__card {
    box-sizing: border-box;
    height: 200px;
    text-align: left;
    padding: 16px; }
  .price__card-body {
    height: 100%;
    border: none;
    display: block;
    text-align: left; }
  .price__card-title_mobile {
    display: block;
    font-family: 'TT Lakes Regular';
    font-size: 14px;
    line-height: 16px;
    color: #7e7e82;
    mix-blend-mode: normal;
    opacity: 0.72;
    margin-bottom: 4px; }
  .price__card-wrap {
    display: block;
    position: static; }
  .price__card-li {
    margin-bottom: 16px; }
    .price__card-li:last-child {
      margin-bottom: 0; }
  .price__card-li_time {
    position: relative;
    left: 0;
    text-align: left; }
  .price__card-btn {
    position: absolute;
    right: 16px;
    bottom: 16px; } }

@media screen and (max-width: 635px) {
  .main__text {
    max-width: 100%; }
  .main__img {
    max-width: 100%;
    width: 100%; }
  .main__text-body {
    margin-right: 0;
    margin-bottom: 1em; }
  .main__content-block {
    display: block; }
  footer {
    text-align: center;
    display: block;
    padding: 1em 0; }
  .footer__text {
    margin-bottom: 1em; }
    .footer__text:last-child {
      margin-bottom: 0; }
  .footer__center {
    max-width: 100%; }
  .footer__right {
    text-align: center;
    max-width: 100%; }
  .navigation__block-center {
    display: none; }
  .navigation__right-bar {
    max-width: 110px; }
  .navigation__block-right:after {
    display: none; } }

@media screen and (max-width: 527.97px) {
  .feedback__close-btn {
    position: relative;
    left: 0;
    margin-bottom: 2em; } }
